
.hero-mid{
    width:100%;
    height: 70vh; 
    position:relative;

}


img{
    width:100%;
    height:100%;
    object-fit: cover;
}
.hero-text{
    position:absolute;
    text-align: center;
    top:55%;
    left:50%;
    transform:translate(-50%,-50%);
    width:100%
}
.hero-text h1{
    font-size: 5rem;
    font-weight: 800;
    background: white;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.hero-text p{
    font-size: 1.3rem;
   color: white;
    padding: 0.5rem 0 2rem 0;  
}
.hero-text .show{
    text-decoration: none;
    background: rgb(91, 147, 236);
    padding: 1rem 2rem;
    margin: 2rem;
    border-radius: 6px;
    font-size: 1.1rem;
    font-weight: bold;
    letter-spacing: 2px;
    color: black;
}
.hero-text .show:hover{
    background-color: blueviolet;
}
.hero-text .hide{
    display: none; 
}
@media screen and (max-width:850px){
    .hero-text h1{
        padding: 10px 20px;
    }
    .hero-text p{
        font-size: 1.1rem;
        padding:0 0 2rem 0;
    }
    .hero-text .show{
        padding: 0.6rem 1.1rem;
        border-radius: 6px;
        font-size: 1rem;
        
    }
    
}