.sign-container form{
    padding-top: 2rem;
    display: flex;
    flex-direction: column;
    width: 50%;
    margin: auto;
}
.sign-container h1{
   text-align: center;
   margin-top: 2rem;
   font-size: 3.3rem;
}
.sign-container input{
   height: 3rem;
   padding: 0 1rem;
   margin-bottom: 2rem;
   border-radius: .3rem;
   border: 1px solid black;
}
.sign-container button{
   display: inline;
   margin-bottom: 1rem;
}
@media screen and (max-width:850px){
   .sign-container{
   padding-top: 2rem;
   }
.sign-container form{
  width:90%; 
  padding-top: 2rem;
} 
}