.form-container form{
     padding-top: 2rem;
     display: flex;
     flex-direction: column;
     width: 50%;
     margin: auto;
}
.form-container h1{
    text-align: center;
    margin-top: 2rem;
    font-size: 3.3rem;
}
.form-container input{
    height: 3rem;
    padding: 0 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid black;
}
.form-container textarea{
    padding: 1rem 1rem;
    margin-bottom: 2rem;
    border-radius: .3rem;
    border: 1px solid black;
}
.form-container button{
    display: inline;
    margin-bottom: 1rem;
}
@media screen and (max-width:850px){
    .form-container{
    padding-top: 2rem;
    }
.form-container form{
   width:90%; 
   padding-top: 2rem;
} 
}
